.sample-container {

    width: 100%;
    height: 60vh;
    margin-top: 12%;
}

.sample-content {
    align-items: center;
    text-align: center;
    justify-content: center;
}

.skill-container {
width:46%;
height: 350px;
position: relative;
background-color: rgba(0, 0, 0, 0.4);
display: inline-block;
margin-left: 2%;
margin-right: 2%;
border-radius: 25px;

}


.skill-title h5{
 letter-spacing: 3px;
    cursor: pointer;
    
}
.skill-title.action h5{
    transform: translateY(10px);
    transition: 0.5s ease;
    text-align: center;
    vertical-align:middle;
}
.skill-title.reaction h5{
    transform: translateY(150px);
    transition: 0.5s ease;
    text-align: center;
}

.skill-details.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(10px);
    transition: 0.5s ease;
}

.skill-details.inactive {
    opacity: 0;
    visibility: hidden;
    transform: translateY(60px);
    transition: 0.5s ease;
}

.skill-details {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    padding: 5px 5px;
    width: 100%;
}

.skill-details::before {
    content: '';
    position: absolute;
    width: 20px;
    background: var(--secondary-bg);
    transform: rotate(45deg);
}

.skill-details ul li {
    color: black;
    padding: 5px 0;
    border-top: 1px solid rgba(0, 0, 0, 0.4);
    
}

.skill-details ul li:hover a{
    color: rgb(212, 33, 9);
}
.skill-details ul li:hover img{
    opacity:1;
}

.dropdowns {
    display: flex;
    margin: 5px auto;
}

.dropdowns img {
    max-width: 20px;
    margin-right: 5px;
    opacity: 0.5;
    transition: var(--speed);
}
.dropdowns a {
    max-width: 250px;
    margin-left: 10px;
}
@media screen and (max-width:770px) {
    .skill-container {
        display:block;
        width: 80%;
        height: 350px;
        background-color: rgba(0, 0, 0, 0.4);
        color: black;
        position: relative;
        margin-right: auto;
        margin-left: auto;
        margin-top: 10px;
        margin-bottom: 10px;
        align-items: center;
        text-align: center;
        border-radius: 25px;
    
    }

    .skill-title h5{
        letter-spacing: 3px;
           cursor: pointer;
           
       }
       .skill-title.action h5{
           transform: translateY(10px);
           transition: 0.5s ease;
       }
       .skill-title.reaction h5{
           transform: translateY(160px);
           transition: 0.5s ease;
       }
}