

.main-heading{
    font-size: 2.0rem;
    padding: 2%;   
    margin: 10px auto;

}
.main-container{
    background-color: rgba(250, 250, 250, 0.3);
    width: 70%;
    margin: 10px auto;
    border-radius: 15px;
}

.link-container{
    font-size: 20px;  
    padding: 5px 10px;
    margin-bottom: 0px;
    background-color: transparent;
    
}
.link-container :hover{
    color: black;    
}
.git-container :hover{
    color: rgb(0, 0, 0);
    
}
.git-text{
    font-size: 1.2rem;
}

@media screen and (max-width = 640px) {

    .main-heading{
        font-size: 0.5rem;
        margin: 7px auto;

    }
    .link-container{
        font-size: 10px;  
        padding: 3px 7px;
        margin-bottom: 0px;
        
    }
    .main-container{
        width: 78%;
        margin: 7px auto;
        border-radius: 10px;
    }
    .git-text{
        font-size: 0.5rem;
    }
}