.hero-img {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    height: 60vh;
    position: relative;
}

.hero-img::before {
    content: "";
    background-size: cover;
    background-position: center top;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1 {
    font-size: 2.4rem;
}

.hero-img p {
    font-size: 1.4rem;
}

.btn-download {
    background-color: transparent;
    border: 2px solid white;
    
    font-size: 1.1rem;
    padding: 0.3rem 1.0rem;
    margin-top: 20px;
   
    position: absolute;
    bottom: 30px;

}

.btn-download:hover {
    background-color: white;
    color: black;
}

@media screen and (max-width: 640px) {
    .hero-img h1 {
        font-size: 2.0rem;
    }

    .hero-img p {
        font-size: 1.0rem;
    }

    .btn-download {
        border-radius: 20px;
        font-size: 1.0rem;
        padding: 0.2rem;
    }
}