.img-container {
    height: 50vh;
    width: 70%;
    margin: 0 auto;
    object-fit: cover;
    border-radius: 30px;
}

.title {
    text-align: center;
    font-size: 2.4rem;
    padding: 0.4rem;
    color: white;
    word-spacing: 20px;
    letter-spacing: 13px;
}


.carousel-container {
    background-color: black;
    padding: 3rem;

    border-radius: 25px;
    height: 100%;
    width: 80%;
    margin: 0 auto;
}


.carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
    padding: 0;
    margin-right: 15%;
    margin-bottom: -1.8rem;
    margin-left: 15%;
    list-style: none;
}

h3 {
    text-align: center;
    padding: 1rem 0;

}

@media screen and (max-width=640px) {
    .img-container {
        height: 300px;
        width: 100%;
        margin: 0 auto;
        object-fit: cover;
        border-radius: 10px;
    }

    .title {
        text-align: center;
        font-size: 1.4rem;
        padding: 0.2rem;
        color: white;
        word-spacing: 5px;
        letter-spacing: 5px;
    }

    .carousel-container {
        background-color: black;
        padding: 0.2rem;
        width: 100%;
        border-radius: 15px;
        height: 30%;
    
        margin: 0 auto;
    }

    h3 {
        text-align: center;
        padding: 0;

    }


}