.about-container {
    width: 100%;
    height: 100%;
    padding: 10px;
    padding-bottom: 10px;
    background-size: cover;
    z-index: -2;
    background: url(../assets/AboutImage.jpg);
    background-color: black;
    opacity: 0.8;
}

.about-content {

    width: 50%;
    height: 100%;
    align-items: center;
    display: inline;
    float: left;

}

.about-content p {
    text-align: center;

}

.dp-Img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    opacity: 0.7;
    display: flex;
}

.dp-Img:hover {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    opacity: 1.0;

    transition: 0.3s;
}

.about-details {
    width: 45%;
    display: inline-block;
    border-radius: 25px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    transition: 0.3s;
    cursor: pointer;
}

.about-details h3 {
    margin: 2px;
}

.about-details p {
    text-align: left;
}

.about-details h4 {
    font-size: 30px;
    padding: 10px;
}

.about-details:hover {
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.3s ease-in-out;
    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
    border: 3px solid white;


}

.aboutme-details {
    width: 100%;
}
.aboutme-details p{
    text-align: center;
}

.aboutme-skills {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 10%;
    display: inline-block;
    
}

.drop-menu.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition: 0.5s ease;
}

.drop-menu.inactive {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: 0.5s ease;
}

.drop-menu {
    position: absolute;
    background-color: rgba(255, 255, 255, 1.0);
    border-radius: 20px;
    padding: 10px 10px;

}

.drop-menu-skills {
    width: 200px;
}

.drop-menu-hobbies {
    width: 250px;
}

.drop-menu::before {
    content: '';
    position: absolute;
    width: 20px;
    background: var(--secondary-bg);
    transform: rotate(45deg);
}

.drop-menu ul li {
    color: black;
    padding: 5px 0;
    border-top: 1px solid rgba(0, 0, 0, 0.4);
}

.drop-menu ul li:hover a {
    color: rgb(212, 33, 9);
    cursor: pointer;
}

.drop-menu ul li:hover img {
    opacity: 1;
    cursor: pointer;
}

.dropdownItem {
    display: flex;
    margin: 5px auto;
}

.dropdownItem img {
    max-width: 20px;
    margin-right: 10px;
    opacity: 0.5;
    transition: var(--speed);
}

.dropdownItem a {
    max-width: 250px;
    margin-left: 10px;
    transition: var(--speed);
}

.prev-exp ul {
    list-style-type: circle;
    line-height: 50px;
}

@media screen and (max-width:640px) {

    .about-content {

        width: 100%;
    }

    .dp-Img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        align-items: center;
        justify-content: center;
        transition: 0.5s;
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
        opacity: 1.0;
        display: flex;
    }

    .dp-Img:hover {
        width: 125px;
        height: 125px;
        border-radius: 50%;
        align-items: center;
        justify-content: center;
        opacity: 1.0;

        transition: 0.3s;
    }

    .about-details {
        width: 100%;
        border: 3px solid white;
        border-radius: 25px;
        margin-left: auto;
        margin-right: auto;
        padding: 10px;
        background-color: rgba(0, 0, 0, 0.4);
        transition: 0.3s;
        cursor: pointer;
    }

    .about-details h3 {
        margin: 2px;
    }

    .about-details p {
        text-align: left;
    }

    .about-details h4 {
        font-size: 20px;
    }



    .drop-menu {
        position: absolute;
        background-color: rgba(255, 255, 255, 1.0);
        border-radius: 20px;
        padding: 5px 5px;

    }

    .drop-menu-skills {
        width: 150px;
    }

    .drop-menu-hobbies {
        width: 200px;
    }

    .drop-menu::before {
        content: '';
        position: absolute;
        width: 15px;
        background: var(--secondary-bg);
        transform: rotate(45deg);
    }

    .drop-menu ul li {
        color: black;
        padding: 3px 0;
        border-top: 1px solid rgba(0, 0, 0, 0.4);
    }


    .dropdownItem {
        display: flex;
        margin: 5px auto;
    }

    .dropdownItem img {
        max-width: 15px;
        margin-right: 5px;
        opacity: 0.5;
        transition: var(--speed);
    }

    .dropdownItem a {
        max-width: 200px;
        margin-left: 5px;
        transition: var(--speed);
        font-size: 12px;
    }



}