.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.intro-dp{
    width:200px;
    height:200px;
    border-radius: 50%;
    transition: 1s;
}
.intro-dp:hover{
    width:220px;
    height:220px;
    transition: 0.2s;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}
.hero{
    height: 100%;
    width: 100%;

}

.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left:50%;
    text-align: center;
    
}

.hero .content h1{
    font-size: 4rem;
    padding: 0.6rem 1.5rem;
    margin: 15px;
   
}
.hero .content h4{
    font-size: 2.2rem;
    padding: 0.2rem 1.5rem;
    margin: 15px;
    
}

/* .hero .content p{
    font-size: 1.4rem;
