    text-transform: uppercase;

} */

.content .btn{
    margin: 1.0rem 0.2rem;
}
.content{
    color:white
}

@media screen  and (max-width: 640px){
    
        .hero .content h1{
            font-size: 3rem;
            
        }
        .hero .content h4{
            font-size: 1.2rem;
            
            
        }
        
        .hero .content p{
            font-size: 1.4rem;
          
        
        } 
        .intro-dp{
            width:100px;
            height:100px;
            border-radius: 50%;
        } 
        .intro-dp:hover{
            width:110px;
            height:110px;
            
        }     
    }
